
.c-select-handle {
  min-width: 50px;
  height: 100%;
  cursor: pointer;
  border: 1px solid #D1D1D1;
  border-radius: 3px;
  padding: 0 20px 0 10px;
  background: #FFFFFF;
  display: flex;
  flex-direction: row;
  position: relative;
}
.c-select-handle.c-size-small {
    height: 24px;
    line-height: 24px;
}
.c-select-handle.c-size-medium {
    height: 28px;
    line-height: 28px;
}
.c-select-handle.c-size-normal {
    height: 32px;
    line-height: 32px;
}
.c-select-handle.c-size-big {
    height: 36px;
    line-height: 36px;
}
.c-select-handle .c-select-label {
    display: inline-block;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    flex: 1 1 auto;
}
.c-select-handle .c-placeholder {
    color: #BCBEC2;
}
.c-select-handle .c-count {
    width: 20px;
    flex: none;
}
.c-select-handle.c-theme-noborder {
    border-color: rgba(0, 0, 0, 0);
}
.c-select-handle.c-theme-noborder + .c-drop-panel {
      margin: 0;
}
.c-select-handle.c-theme-tag {
    display: inline-block;
    height: auto;
    min-height: 32px;
    line-height: normal;
    vertical-align: top;
    padding-top: 5px;
}
.c-select-handle.c-theme-tag .c-tag {
      flex: none;
      line-height: initial;
      margin: 0 5px 3px 0;
}
.c-select-handle.c-theme-tag input[type='text'] {
      border: none;
      font-size: 12px;
      height: 20px;
}
.c-select-handle.c-theme-tag .c-temp {
      position: absolute;
      font-size: 12px;
      top: -999999px;
}
.c-select-handle.disable {
    border-color: #E1E3E6;
    color: #E1E3E6;
    cursor: not-allowed;
}
.c-select-handle .c-select-chosen input[type='text'] {
    padding-right: 20px !important;
    cursor: pointer;
}
.c-select-handle .c-select-arrow {
    position: absolute;
    top: 0;
    right: 5px;
}
.c-select-handle.active .c-select-arrow {
    transform: rotate(180deg);
}



