
.c-tabs .c-tab-bar {
  display: inline-block;
}
.c-tabs .c-tab-bar .c-tab {
    display: inline-block;
    height: 36px;
    line-height: 36px;
    font-size: 14px;
    text-align: center;
    position: relative;
}
.c-tabs .c-tab-bar .c-tab .c-tab-close {
      visibility: hidden;
      cursor: pointer;
      float: right;
      color: #c8cacc;
}
.c-tabs .c-tab-bar .c-tab .c-tab-close:hover {
        color: #f56262;
}
.c-tabs .c-tab-bar .c-tab .c-tab-icon {
      margin-right: .5em;
}
.c-tabs .c-tab-bar .c-tab:hover .c-tab-close {
      visibility: visible;
}
.c-tabs .c-tab-bar.c-tab-card .c-tab {
    background: #FFFFFF;
    color: #354052;
    border: 1px solid #D1D1D1;
    border-right: none;
    padding: 0 1.5em;
}
.c-tabs .c-tab-bar.c-tab-card .c-tab .c-tab-close {
      position: absolute;
      right: 3px;
}
.c-tabs .c-tab-bar.c-tab-card .c-tab:hover {
      cursor: pointer;
}
.c-tabs .c-tab-bar.c-tab-card .c-tab:first-child {
      border-top-left-radius: 3px;
      border-bottom-left-radius: 3px;
}
.c-tabs .c-tab-bar.c-tab-card .c-tab:last-child {
      border-right: 1px solid #D1D1D1;
      border-top-right-radius: 3px;
      border-bottom-right-radius: 3px;
}
.c-tabs .c-tab-bar.c-tab-card .c-tab.c-checked {
      background: #0ea1eb;
      color: #FFFFFF;
      border-color: #0ea1eb;
}
.c-tabs .c-tab-bar.c-tab-card .c-tab.c-checked:hover {
        cursor: default;
}
.c-tabs .c-tab-bar.c-tab-card .c-tab.c-checked .c-tab-close {
        color: #FFFFFF;
}
.c-tabs .c-tab-bar.c-tab-card .c-tab.c-checked + .c-tab {
        border-left-color: #0ea1eb;
}
.c-tabs .c-tab-bar.c-tab-normal ~ .c-tab-panel {
    border-top: 1px solid #D1D1D1;
}
.c-tabs .c-tab-bar.c-tab-normal .c-tab {
    color: #354052;
    top: 1px;
}
.c-tabs .c-tab-bar.c-tab-normal .c-tab .c-tab-close {
      float: right;
      margin-left: 15px;
}
.c-tabs .c-tab-bar.c-tab-normal .c-tab + .c-tab {
      margin-left: 22px;
}
.c-tabs .c-tab-bar.c-tab-normal .c-tab:hover {
      cursor: pointer;
}
.c-tabs .c-tab-bar.c-tab-normal .c-tab.c-checked {
      color: #0ea1eb;
      border-bottom: 2px solid #0ea1eb;
}
.c-tabs .c-tab-bar.c-center {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}
.c-tabs .c-tab-bar.c-right {
    position: relative;
    left: 100%;
    transform: translateX(-100%);
}
.c-tabs.c-tab-vertical {
  display: flex;
  flex-direction: row;
}
.c-tabs.c-tab-vertical > .c-tab-bar {
    flex: none;
}
.c-tabs.c-tab-vertical > .c-tab-bar > .c-tab {
      display: block;
      margin-left: 0 !important;
      left: 1px;
      top: 0;
      padding-right: 20px;
}
.c-tabs.c-tab-vertical > .c-tab-bar > .c-tab .c-tab-close {
        right: 5px;
}
.c-tabs.c-tab-vertical > .c-tab-bar > .c-tab.c-checked {
        border-right: 2px solid #0ea1eb;
        border-bottom: none;
}
.c-tabs.c-tab-vertical > .c-tab-panel {
    flex: 1;
    border-top: none !important;
    border-left: 1px solid #D1D1D1;
    padding-left: 20px;
    padding-top: 0 !important;
}



