
.c-tag {
  display: inline-block;
  line-height: 1em;
  padding: 3px 6px;
  border-radius: 3px;
  font-size: 12px;
  background: #f0faff;
  color: #09aef5;
  border: 1px solid #09aef5;
}
.c-tag .c-icon {
    color: #0ea1eb;
    margin-left: 3px;
    cursor: pointer;
}



