
.c-pie-chart {
  background: #FFFFFF;
  width: 100%;
  position: relative;
  height: 300px;
}
.c-pie-chart .c-charts-main, .c-pie-chart .c-charts-empty {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}
.c-pie-chart .c-charts-empty {
    font-size: 14px;
    color: #75787D;
    text-align: center;
    background: #FFFFFF;
}



