
.scroll-demo {
  height: 500px;
  max-width: 500px;
  border: 1px solid #D1D1D1;
}
.scroll-demo .child {
    height: 200px;
    width: 200px;
    margin: 5px 0;
    line-height: 200px;
    background: #f3f5ef;
    text-align: center;
}
.scroll-demo .margin-panel {
    margin: 0 510px;
    display: inline-block;
}
.scroll-demo .handle-area {
    background: #ffad00e6;
}
.scroll-demo .handle-area .child {
      background: rgba(52, 52, 52, 0.1);
}
.scroll-demo .inner-container {
    background: #d0cabc;
}



