
.c-radio-list {
  display: inline-block;
  color: #354052;
  white-space: nowrap;
  font-size: 14px;
}
.c-radio-list .c-radio-item {
    cursor: pointer;
}
.c-radio-list .c-radio-item .c-cycle {
      vertical-align: middle;
      margin-right: 10px;
      display: inline-block;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      border: 1px solid #D1D1D1;
}
.c-radio-list .c-radio-item.c-checked .c-cycle {
      border: 1px solid #0ea1eb;
      position: relative;
}
.c-radio-list .c-radio-item.c-checked .c-cycle:after {
        content: '';
        display: inline-block;
        vertical-align: middle;
        position: absolute;
        left: 3px;
        top: 3px;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: #0ea1eb;
}
.c-radio-list .c-radio-item.c-disable {
      cursor: not-allowed;
}
.c-radio-list .c-radio-item.c-disable .c-cycle {
        border-color: #E1E3E6;
}
.c-radio-list .c-radio-item.c-disable .c-cycle:after {
          background: #E1E3E6;
}
.c-radio-list.c-radio-row .c-radio-item {
    float: left;
    margin-left: 20px;
}
.c-radio-list.c-radio-row .c-radio-item:first-child {
      margin-left: 0;
}



