
.c-notification {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 3px;
  background: #FFFFFF;
  padding: 10px;
  box-shadow: 0 1px 4px rgba(53, 64, 82, 0.5);
  animation-delay: 0s;
  animation-duration: .2s;
  animation-name: fadeInRight;
}
.c-notification .c-notification_ {
    display: flex;
    flex-direction: row;
}
.c-notification .c-notification_ i {
      flex: none;
      margin-right: 7px;
      font-size: 16px;
}
.c-notification .c-notification_ .c-notification-center {
      flex: 1;
}
.c-notification .c-notification_ .c-notification-center .c-notification-title {
        font-size: 14px;
}
.c-notification .c-notification_ .c-notification-center .c-notification-content {
        font-size: 12px;
        line-height: 20px;
        margin: 10px 0;
}
.c-notification .c-button {
    height: 26px;
    line-height: 26px;
    float: right;
}
div[class^='c-notification-container-'] {
  z-index: 999999999;
}



