
.c-cld-task {
  position: absolute;
  font-size: 12px;
  height: 18px;
  line-height: 18px;
  border-radius: 3px;
  background: #0ea1eb;
  margin-bottom: 5px;
  color: #FFFFFF;
  padding: 0 5px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  word-break: keep-all;
}
.c-cld-task:hover {
    background: #1eb4ff;
}
.c-cld-task .c-cld-mark {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    border: 2px solid #FFFFFF;
    background: #f55858;
}



