
.c-dialog {
  border: 1px solid #D1D1D1;
  border-radius: 3px;
  background: #FFFFFF;
  display: flex;
  flex-direction: column;
  position: fixed;
  z-index: 999999999;
}
.c-dialog .c-dialog-head {
    height: 58px;
    line-height: 58px;
    padding: 0 20px;
    font-size: 18px;
    color: #354052;
    border-bottom: 1px solid #D1D1D1;
    flex: none;
}
.c-dialog .c-dialog-content {
    padding: 20px;
    flex: 1 1 auto;
    overflow: auto;
}
.c-dialog .c-dialog-foot {
    padding: 20px;
    flex: none;
}
.c-dialog .c-dialog-foot .c-dialog-cancel, .c-dialog .c-dialog-foot .c-dialog-confirm {
      float: right;
}
.c-dialog .c-dialog-foot .c-dialog-cancel {
      margin-right: 10px;
}
.c-dialog-layer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(53, 64, 82, 0.5);
  z-index: 999999999;
}



