
.c-flow {
  background: #fff7e6;
  width: 100%;
  position: relative;
}
.c-flow .c-flow-main, .c-flow .c-flow-empty {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}
.c-flow .c-flow-empty {
    font-size: 14px;
    color: #75787D;
    text-align: center;
    background: #FFFFFF;
}



