
.c-data-grid {
  border: 1px solid #D1D1D1;
  border-radius: 3px;
  color: #354052;
  overflow-x: auto;
}
.c-data-grid .c-grid-container {
    display: flex;
    flex-direction: row;
    position: relative;
}
.c-data-grid .c-grid-container .c-grid-left {
      flex: none;
      position: relative;
      border-right: 1px solid #D1D1D1;
      z-index: 101;
}
.c-data-grid .c-grid-container .c-grid-right {
      flex: none;
      position: relative;
      border-left: 1px solid #D1D1D1;
      box-shadow: -6px 0 6px -4px rgba(0, 0, 0, 0.2);
      z-index: 101;
}
.c-data-grid .c-grid-container .c-grid-right .c-grid-cell:first-child, .c-data-grid .c-grid-container .c-grid-right .c-grid-header-cell:first-child {
        text-align: right;
}
.c-data-grid .c-grid-container .c-grid-center {
      flex: 1 1 auto;
      position: relative;
      z-index: 100;
      overflow-x: auto;
}
.c-data-grid .c-grid-container .c-grid-center .c-grid-cell:first-child, .c-data-grid .c-grid-container .c-grid-center .c-grid-header-cell:first-child {
        text-align: right;
}
.c-data-grid .c-grid-container .c-grid {
      width: auto;
      min-width: 100%;
      border-collapse: collapse;
      font-size: 14px;
}
.c-data-grid .c-grid-container .c-grid .c-grid-header .c-grid-header-row {
        background: #e6e8eb;
}
.c-data-grid .c-grid-container .c-grid .c-grid-body .c-grid-row {
        background: #FFFFFF;
}
.c-data-grid .c-grid-container .c-grid .c-grid-body .c-grid-row:nth-child(2n) {
          background: #FAFAFA;
}
.c-data-grid .c-grid-container .c-grid .c-grid-body .c-grid-row:hover {
          background: #fff;
}
.c-data-grid .c-grid-container .c-grid .c-grid-body .c-grid-row:last-child .c-grid-cell {
          border-bottom: none;
}
.c-data-grid .c-hidden-structure {
    display: none;
}
.c-data-grid .c-hidden-structure .c-grid-hover-color {
      background: #F0FAFF;
}
.c-data-grid .c-grid-empty {
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-size: 14px;
    color: #75787D;
}



