
.c-charts {
  background: #fff7e6;
  width: 100%;
  position: relative;
}
.c-charts .c-charts-main, .c-charts .c-charts-empty {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}
.c-charts .c-charts-empty {
    font-size: 14px;
    color: #75787D;
    text-align: center;
    background: #FFFFFF;
}



