
.c-input {
  display: inline-block;
  border: 1px solid #D1D1D1;
  border-radius: 3px;
  font-size: 14px;
  height: 28px;
  line-height: 28px;
  overflow: hidden;
  position: relative;
  vertical-align: middle;
}
.c-input .temp {
    font-size: inherit;
    line-height: inherit;
    padding: 0;
    position: absolute;
    visibility: hidden;
    top: -999px;
    white-space: nowrap;
}
.c-input .c-tooltip-trigger {
    width: 100%;
    height: 100%;
    vertical-align: top;
}
.c-input .c-tooltip-trigger .c-input-input {
      font-size: inherit;
      display: block;
      width: 100%;
      height: 100%;
      line-height: inherit;
      border: none;
      border-radius: 3px;
}
.c-input.c-active {
    border-color: #14abf7;
}
.c-input.c-error {
    border-color: #f55858;
}
.c-input.c-size-small {
    height: 24px;
    line-height: 24px;
}
.c-input.c-size-medium {
    height: 28px;
    line-height: 28px;
}
.c-input.c-size-normal {
    height: 32px;
    line-height: 32px;
}
.c-input.c-size-big {
    height: 36px;
    line-height: 36px;
}
.c-input .c-input-icon, .c-input .c-input-clear {
    font-size: 12px;
    color: #bcbec2;
    position: absolute;
    top: 0;
}
.c-input .c-input-icon {
    left: 8px;
}
.c-input .c-input-clear {
    right: 8px;
    cursor: pointer;
}
.c-tooltip-error {
  background: #f55858 !important;
  color: #FFFFFF !important;
}
.c-tooltip-error:after {
    background: #f55858 !important;
}



