
.doc-panel {
  box-sizing: border-box;
  padding: 15px;
  width: 100%;
  max-width: 1000px;
}
.doc-panel .table {
    margin-top: 10px;
    border-collapse: collapse;
    width: 100%;
    background-color: #fff;
    color: #5e6d82;
    font-size: 14px;
    line-height: 1.5em;
}
.doc-panel .table thead {
      background: #eff2f7;
}
.doc-panel .table td, .doc-panel .table th {
      border-bottom: 1px solid #eaeefb;
      padding: 10px;
      max-width: 250px;
}
.doc-panel .table th {
      text-align: left;
      border-top: 1px solid #eaeefb;
      background-color: #eff2f7;
      white-space: nowrap;
}
.doc-panel .table tbody td:first-child, .doc-panel .table th:first-child {
      padding-left: 10px;
      background: #fcfcfc;
}



