
.c-grid-header-cell {
  border: 1px solid #D1D1D1;
  border-top: none;
  padding: 10px;
  font-weight: normal;
  color: #354052;
  text-align: right;
  vertical-align: middle;
  min-width: 120px;
  line-height: 20px;
  white-space: nowrap;
}
.c-grid-header-cell:first-child {
    border-left: none;
    text-align: left;
}
.c-grid-header-cell:last-child {
    border-right: none;
}
.c-grid-header-cell.c-sort-able {
    cursor: pointer;
    text-decoration: underline;
}
.c-grid-header-cell .c-sort-icon {
    float: right;
    color: #75787D;
    position: relative;
    margin-left: 5px;
    position: relative;
    right: -5px;
}



