
.c-fore-chart {
  background: #fff7e6;
  width: 100%;
  position: relative;
}
.c-fore-chart .c-fore-chart-main, .c-fore-chart .c-fore-chart-empty {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}
.c-fore-chart .c-fore-chart-empty {
    font-size: 14px;
    color: #75787D;
    text-align: center;
    background: #FFFFFF;
}



