
.c-checkbox {
  display: inline-block;
  color: #354052;
  cursor: pointer;
  white-space: nowrap;
  font-size: 14px;
}
.c-checkbox.checked .c-box {
    background: #0ea1eb;
    color: #FFFFFF;
    border-color: #0ea1eb;
}
.c-checkbox.disable {
    color: #E1E3E6;
    cursor: not-allowed;
}
.c-checkbox.disable .c-box {
      border: 1px solid #E1E3E6;
      background: #FAFAFA;
}
.c-checkbox.disable.checked .c-box {
    border: none;
    background: #E1E3E6;
}
.c-checkbox .c-box {
    display: inline-block;
    vertical-align: middle;
    width: 16px;
    height: 16px;
    line-height: 16px;
    border: 1px solid #D1D1D1;
    text-align: center;
    margin-right: 5px;
    border-radius: 3px;
}
.c-checkbox .check-small {
    display: inline-block;
    background: #0ea1eb;
    width: 8px;
    height: 8px;
    border-radius: 3px;
    vertical-align: top;
    margin-top: 3px;
}



