
.c-reload {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #FFFFFF;
  opacity: .9;
}
.c-reload .c-reload-msg {
    font-size: 14px;
    color: #354052;
}
.c-reload.c-small .c-reload-main {
    position: relative;
    width: 225px;
    margin: -20px auto 0 auto;
    top: 50%;
    white-space: nowrap;
    overflow: visible;
}
.c-reload.c-small .c-reload-main img {
      margin-right: 10px;
      vertical-align: middle;
}
.c-reload.c-small .c-reload-main .c-button {
      border: none !important;
      background: none !important;
      transform: translateX(-25px);
}
.c-reload.c-small .c-reload-main .c-button:hover {
        background: none !important;
}
.c-reload.c-bigger .c-reload-main {
    position: relative;
    width: 225px;
    margin: -80px auto 0 auto;
    top: 50%;
    white-space: nowrap;
    overflow: visible;
}
.c-reload.c-bigger .c-reload-main img, .c-reload.c-bigger .c-reload-main .c-reload-msg, .c-reload.c-bigger .c-reload-main .c-button {
      display: block;
      margin: 20px auto 0 auto;
      text-align: center;
}



