
.c-cld-day {
  border: 1px solid #D1D1D1;
  vertical-align: top;
  padding: 5px;
  color: #404245;
}
.c-cld-day:hover {
    background: #F0FAFF;
    cursor: pointer;
}
.c-cld-day .c-cld-day-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow: visible;
    position: relative;
}
.c-cld-day .c-cld-day-container .c-cld-label {
      text-align: right;
      flex: none;
      height: 25px;
}
.c-cld-day .c-cld-day-container .c-cld-task-panel {
      flex: 1;
      overflow: visible;
      position: relative;
}
.c-cld-day .c-cld-day-container .c-cld-task-list {
      flex: 1;
}
.c-cld-day .c-cld-day-container .c-cld-task-list .c-cld-task {
        font-size: 12px;
        height: 18px;
        line-height: 18px;
        border-radius: 3px;
        background: #0ea1eb;
        margin-bottom: 5px;
        color: #FFFFFF;
        padding: 0 5px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        word-break: keep-all;
}
.c-cld-day .c-cld-day-container .c-cld-task-list .c-cld-task:hover {
          background: #1eb4ff;
}
.c-cld-day.c-cld-today .c-day-num {
    display: inline-block;
    width: 25px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    border-radius: 100%;
    background: #0ea1eb;
    margin-right: 5px;
    color: #FFFFFF;
}
.c-cld-day.c-cld-active {
    background: #F0FAFF;
}



