
.c-grid-cell {
  border: 1px solid #D1D1D1;
  padding: 10px;
  text-align: right;
  vertical-align: middle;
  word-wrap: break-word;
  min-width: 120px;
}
.c-grid-cell:first-child {
    border-left: none;
    text-align: left;
}
.c-grid-cell:last-child {
    border-right: none;
}
.c-grid-cell.c-click-able {
    cursor: pointer;
}
.c-grid-cell.c-click-able:hover {
      text-decoration: underline;
}



