
.c-pagination {
  font-size: 14px;
  padding: 0 20px;
}
.c-pagination .c-pg-txt {
    color: #75787D;
}
.c-pagination .c-pg-txt .c-pg-num {
      margin: 0 5px;
}
.c-pagination .c-button {
    height: 34px;
    line-height: 34px;
}
.c-pagination .c-pg-pre, .c-pagination .c-pg-next {
    width: 34px;
}



