
.c-date-picker {
  display: inline-block;
  font-size: 14px;
}
.c-date-picker .c-picker-handle {
    cursor: pointer;
    display: inline-block;
    height: 32px;
    line-height: 32px;
    border-radius: 3px;
    border: 1px solid #D1D1D1;
    padding: 0 10px;
    width: 220px;
    text-align: center;
    background: #FFFFFF;
}
.c-date-picker .c-picker-panel {
    width: auto;
    border: 1px solid #D1D1D1;
    border-radius: 3px;
    margin-top: 5px;
    padding: 10px 10px 5px 10px;
    background: #FFFFFF;
}
.c-date-picker .c-picker-panel .c-picker-top {
      height: 32px;
      line-height: 32px;
      text-align: center;
      font-weight: bolder;
}
.c-date-picker .c-picker-panel .c-picker-top i {
        cursor: pointer;
}
.c-date-picker .c-picker-panel .c-picker-top ._c-icon {
        float: left;
}
.c-date-picker .c-picker-panel .c-picker-top .c-icon_ {
        float: right;
}
.c-date-picker .c-picker-panel .c-cld {
      border-collapse: collapse;
}
.c-date-picker .c-picker-panel .c-cld .c-cld-title td {
        padding: 10px 5px;
        text-align: right;
        font-weight: bolder;
        color: #354052;
}
.c-date-picker .c-picker-panel .c-cld .c-cld-body {
        font-size: 14px;
        color: #75787D;
}
.c-date-picker .c-picker-panel .c-cld .c-cld-secondary {
        color: #bcbec2 !important;
}
.c-date-picker .c-picker-panel .c-to-today {
      border-top: 1px solid #D1D1D1;
      height: 32px;
      line-height: 36px;
      cursor: pointer;
      color: #0ea1eb;
      text-align: center;
      margin-top: 5px;
}



