
.c-calendar-container {
  background: #FFFFFF;
}
.c-calendar-container .c-cld-toolbar {
    text-align: center;
}
.c-calendar-container .c-cld-toolbar .c-cld-year {
      float: left;
}
.c-calendar-container .c-cld-toolbar .c-cld-day-control {
      float: right;
}
.c-calendar-container .c-cld {
    border-collapse: collapse;
}
.c-calendar-container .c-cld .c-cld-title td {
      padding: 10px 5px;
      text-align: right;
}
.c-calendar-container .c-cld .c-cld-body {
      font-size: 14px;
      color: #75787D;
}
.c-calendar-container .c-cld .c-cld-secondary {
      color: #bcbec2 !important;
}



