
.c-loading {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  background: rgba(255, 255, 255, 0.9);
  z-index: 999999999;
}
.c-loading .c-circle {
    position: relative;
    margin: 0 auto;
    border: 5px solid #0ea1eb;
    border-left-color: #e6e8eb;
    border-top-color: #e6e8eb;
    border-bottom-color: #e6e8eb;
    border-radius: 50%;
    animation: c-loading 1s linear infinite;
}
.c-loading .c-loading-desc {
    position: relative;
    font-size: 14px;
    color: #354052;
    white-space: nowrap;
}
@keyframes c-loading {
0% {
    transform: rotate(-45deg);
}
100% {
    transform: rotate(315deg);
}
}



