
.c-radio-button {
  display: inline-block;
}
.c-radio-button .c-radio-item {
    border-radius: 0;
    position: relative;
    margin-left: -1px;
}
.c-radio-button .c-radio-item:first-child {
      border-top-left-radius: 3px;
      border-bottom-left-radius: 3px;
}
.c-radio-button .c-radio-item:last-child {
      border-right-width: 1px;
      border-top-right-radius: 3px;
      border-bottom-right-radius: 3px;
}
.c-radio-button .c-radio-item:hover, .c-radio-button .c-radio-item.c-checked {
      z-index: 100;
}
.c-radio-button .c-radio-item.c-disable, .c-radio-button .c-radio-item.c-disable:hover {
      z-index: 99;
}



