
.c-button {
  display: inline-block;
  cursor: pointer;
  padding: 0 1.5em;
  height: 32px;
  line-height: 32px;
  font-size: 14px;
  border-radius: 3px;
  white-space: nowrap;
  text-align: center;
  user-select: none;
}
.c-button.c-button-icon {
    padding: 0 0.5em !important;
}
.c-button.c-button-icon .c-icon {
      margin: 0;
}
.c-button.c-button-size-small {
    font-size: 12px;
    height: 24px;
    line-height: 24px;
    padding: 0 1em;
}
.c-button.c-button-size-large {
    font-size: 16px;
    height: 36px;
    line-height: 36px;
    padding: 0 3em;
}
.c-button.c-button-normal-normal {
    border: 1px solid #D1D1D1;
    color: #354052;
    background: #fafafa;
}
.c-button.c-button-normal-normal:hover {
      background: #fff;
}
.c-button.c-button-normal-normal:active, .c-button.c-button-normal-normal.c-checked {
      background: #F0FAFF;
}
.c-button.c-button-normal-primary {
    color: #FFFFFF;
    border: 1px solid #0ea1eb;
    background: #0ea1eb;
}
.c-button.c-button-normal-primary:hover {
      border-color: #1eb4ff;
      background: #1eb4ff;
}
.c-button.c-button-normal-primary:active, .c-button.c-button-normal-primary.c-checked {
      border-color: #14abf7;
      background: #14abf7;
}
.c-button.c-button-normal-danger {
    color: #FFFFFF;
    border: 1px solid #f55858;
    background: #f55858;
}
.c-button.c-button-normal-danger:hover {
      border-color: #ff7575;
      background: #ff7575;
}
.c-button.c-button-normal-danger:active, .c-button.c-button-normal-danger.c-checked {
      border-color: #f56262;
      background: #f56262;
}
.c-button.c-button-normal-secondary {
    color: #FFFFFF;
    border: 1px solid #0EB7EB;
    background: #0EB7EB;
}
.c-button.c-button-normal-secondary:hover {
      border-color: #28BFEE;
      background: #28BFEE;
}
.c-button.c-button-normal-secondary:active, .c-button.c-button-normal-secondary.c-checked {
      border-color: #0DABDC;
      background: #0DABDC;
}
.c-button.c-button-normal-success {
    color: #FFFFFF;
    border: 1px solid #29bd76;
    background: #29bd76;
}
.c-button.c-button-normal-success:hover {
      border-color: #31cc81;
      background: #31cc81;
}
.c-button.c-button-normal-success:active, .c-button.c-button-normal-success.c-checked {
      border-color: #22bf73;
      background: #22bf73;
}
.c-button.c-button-normal-warning {
    color: #FFFFFF;
    border: 1px solid #e07800;
    background: #e07800;
}
.c-button.c-button-normal-warning:hover {
      border-color: #ed8e21;
      background: #ed8e21;
}
.c-button.c-button-normal-warning:active, .c-button.c-button-normal-warning.c-checked {
      border-color: #e68312;
      background: #e68312;
}
.c-button.c-button-border-normal {
    border: 1px solid #D1D1D1;
    color: #354052;
    background: #FFFFFF;
}
.c-button.c-button-border-normal:hover {
      color: #0ea1eb;
      border-color: #0ea1eb;
      background: #FFFFFF;
}
.c-button.c-button-border-normal:active, .c-button.c-button-border-normal.c-checked {
      color: #0ea1eb;
      border-color: #0ea1eb;
      background: #f0faff;
}
.c-button.c-button-border-primary {
    color: #0ea1eb;
    border: 1px solid #0ea1eb;
    background: #FFFFFF;
}
.c-button.c-button-border-primary:hover {
      background: #f0faff;
}
.c-button.c-button-border-primary:active, .c-button.c-button-border-primary.c-checked {
      background: #dbf3ff;
}
.c-button.c-button-border-danger {
    color: #f55858;
    border: 1px solid #f55858;
    background: #FFFFFF;
}
.c-button.c-button-border-danger:hover {
      background: #ffebeb;
}
.c-button.c-button-border-danger:active, .c-button.c-button-border-danger.c-checked {
      background: #FFdbdb;
}
.c-button.c-button-border-secondary {
    color: #0EA1EB;
    border: 1px solid #0EA1EB;
    background: #FFFFFF;
}
.c-button.c-button-border-secondary:hover {
      background: #f0faff;
}
.c-button.c-button-border-secondary:active, .c-button.c-button-border-secondary.c-checked {
      background: #e0f5ff;
}
.c-button.c-button-border-success {
    color: #29bd76;
    border: 1px solid #29bd76;
    background: #FFFFFF;
}
.c-button.c-button-border-success:hover {
      background: #ebfff5;
}
.c-button.c-button-border-success:active, .c-button.c-button-border-success.c-checked {
      background: #dbffed;
}
.c-button.c-button-border-warning {
    color: #e07800;
    border: 1px solid #e07800;
    background: #FFFFFF;
}
.c-button.c-button-border-warning:hover {
      background: #fff7e6;
}
.c-button.c-button-border-warning:active, .c-button.c-button-border-warning.c-checked {
      background: #fff2d6;
}
.c-button.c-disable {
    border-color: #E1E3E6;
    color: #FFFFFF;
    background: #E1E3E6;
    cursor: not-allowed;
}
.c-button.c-disable:hover, .c-button.c-disable:active {
      background: #E1E3E6;
      color: #FFFFFF;
      border-color: #E1E3E6;
}



