
.c-tooltip-dark, .c-tooltip-light {
  display: inline-block;
  position: absolute;
  background: #354052;
  color: #FFFFFF;
  padding: 10px;
  border-radius: 3px;
  font-size: 12px;
  line-height: 18px;
  box-shadow: 0 1px 4px rgba(53, 64, 82, 0.5);
  z-index: 999999999;
}
.c-tooltip-dark:before, .c-tooltip-light:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 3px;
    top: 0;
    left: 0;
    z-index: 10;
}
.c-tooltip-dark *, .c-tooltip-light * {
    position: relative;
    z-index: 11;
}
.c-tooltip-dark:after, .c-tooltip-light:after {
    content: '';
    display: inline-block;
    width: 10px;
    height: 10px;
    transform: rotate(45deg);
    transform-origin: center;
    position: absolute;
    background: #354052;
    z-index: -10;
}
.c-tooltip-dark.c-top-left:after, .c-tooltip-light.c-top-left:after {
    left: 10px;
    bottom: -4px;
}
.c-tooltip-dark.c-top:after, .c-tooltip-light.c-top:after {
    left: 50%;
    bottom: -4px;
    margin-left: -5px;
}
.c-tooltip-dark.c-top-right:after, .c-tooltip-light.c-top-right:after {
    bottom: -4px;
    right: 10px;
}
.c-tooltip-dark.c-left-top:after, .c-tooltip-light.c-left-top:after {
    right: -4px;
    top: 10px;
}
.c-tooltip-dark.c-left:after, .c-tooltip-light.c-left:after {
    right: -4px;
    top: 50%;
    margin-top: -5px;
}
.c-tooltip-dark.c-left-bottom:after, .c-tooltip-light.c-left-bottom:after {
    right: -4px;
    bottom: 10px;
}
.c-tooltip-dark.c-right-top:after, .c-tooltip-light.c-right-top:after {
    left: -4px;
    top: 10px;
}
.c-tooltip-dark.c-right:after, .c-tooltip-light.c-right:after {
    left: -4px;
    top: 50%;
    margin-top: -5px;
}
.c-tooltip-dark.c-right-bottom:after, .c-tooltip-light.c-right-bottom:after {
    left: -4px;
    bottom: 10px;
}
.c-tooltip-dark.c-bottom-left:after, .c-tooltip-light.c-bottom-left:after {
    left: 10px;
    top: -4px;
}
.c-tooltip-dark.c-bottom:after, .c-tooltip-light.c-bottom:after {
    left: 50%;
    top: -4px;
    margin-left: -5px;
}
.c-tooltip-dark.c-bottom-right:after, .c-tooltip-light.c-bottom-right:after {
    top: -4px;
    right: 10px;
}
.c-tooltip-dark .c-tooltip-close, .c-tooltip-light .c-tooltip-close {
    color: #FFFFFF;
    text-decoration: underline;
    margin-left: 10px;
    cursor: pointer;
}
.c-tooltip-light {
  background: #FAFAFA;
  color: #354052;
}
.c-tooltip-light .c-tooltip-close {
    color: #0ea1eb;
}
.c-tooltip-light:after {
    background: #FAFAFA;
}
.c-tooltip-light.c-top:after, .c-tooltip-light.c-top-left:after, .c-tooltip-light.c-top-right:after {
    box-shadow: 3px 3px 3px rgba(53, 64, 82, 0.2);
}
.c-tooltip-light.c-bottom:after, .c-tooltip-light.c-bottom-left:after, .c-tooltip-light.c-bottom-right:after {
    box-shadow: -1px -1px 1px rgba(53, 64, 82, 0.1);
}
.c-tooltip-light.c-left-top:after, .c-tooltip-light.c-left:after, .c-tooltip-light.c-left-bottom:after {
    box-shadow: 2px -2px 2px rgba(53, 64, 82, 0.2);
}
.c-tooltip-light.c-right:after, .c-tooltip-light.c-right-top:after, .c-tooltip-light.c-right-bottom:after {
    box-shadow: -2px 2px 2px rgba(53, 64, 82, 0.2);
}
.c-tooltip-trigger {
  display: inline-block;
}



