
.c-select {
  display: inline-block;
  font-size: 14px;
  position: relative;
}
.c-select .option-list-control {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #D1D1D1;
}
.c-select .option-list-control .c-option:hover {
      background: none;
}
.c-select .option-list-control .c-option .c-checkbox {
      padding: 0 5px;
}
.c-select .c-drop-panel {
    position: absolute;
    z-index: 999999999;
    border: 1px solid #D1D1D1;
    border-radius: 3px;
    min-width: 100%;
    max-height: 335px;
    width: auto;
    background: #FFFFFF;
    box-shadow: 0 1px 4px rgba(53, 64, 82, 0.5);
    display: flex;
    flex-direction: column;
}
.c-select .c-drop-panel .c-fixed {
      flex: none;
      padding: 5px;
}
.c-select .c-drop-panel .c-context {
      position: relative;
}
.c-select .c-drop-panel .c-context .loading-wrap {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 34px;
}
.c-select .c-drop-panel .c-content {
      height: 100%;
      max-height: 260px;
      flex: 1 1 auto;
      overflow-y: auto;
}
.c-select .c-drop-panel .c-content .c-error {
        color: #E1E3E6;
        cursor: default;
        width: 150px;
        padding: 0 10px;
}
.c-select .c-drop-panel .c-clear {
      font-size: 12px;
      color: #09aef5;
      margin-left: 10px;
      height: 34px;
      line-height: 34px;
      white-space: nowrap;
}



