
.c-message {
  position: fixed;
  top: 50px;
  left: 50%;
  margin-left: -230px;
  width: 460px;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  color: #FFFFFF;
  border-radius: 3px;
  box-shadow: 0 1px 4px rgba(53, 64, 82, 0.5);
  font-size: 14px;
  z-index: 999999999;
}
.c-message .c-icon {
    margin-right: 10px;
}
.c-message.c-success {
    background: #29bd76;
}
.c-message.c-error {
    background: #f55858;
}
.c-message.c-info {
    background: #09aef5;
}
.c-message .c-close {
    float: right;
    cursor: pointer;
}



