@import "constants";
@import "animate.css";
* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); //  remove tap highlight color for mobile safari
  font-family: $font-family; }

*:before,
*:after {
  box-sizing: border-box; }
// unify the setting of elements's margin and padding for browsers
div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td,hr,button,article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }


// Reset fonts for relevant elements
button,input,select,textarea {
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  outline: none; }

ul,
li,
ol {
  list-style: none; }


// Remove the clear button of a text input control in IE10+
input::-ms-clear, input::-ms-reveal {
  display: none; }


// Links
a {
  background: transparent;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  transition: color .3s ease;

  &:focus {
    text-decoration: underline;
    text-decoration-skip-ink: auto; } }

[class^='c-'], [class*=' c-'] {
  box-sizing: border-box;
  font-family: $font-family;
  outline: none;
  list-style: none;
  * {
    @extend [class^='c-'];
    text-decoration: none; } }
[class^='cicon-'], [class*=' cicon-'] {
  font-family: "cleverui" !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }



