
.c-day {
  font-size: 13px;
  cursor: pointer;
  height: 28px;
  line-height: 28px;
  width: 28px;
  text-align: center;
  border-radius: 3px;
}
.c-day.c-today {
    color: #0ea1eb;
}
.c-day.c-other-month {
    color: #c8cacc;
}
.c-day.c-highlight-day {
    background: #F0FAFF;
}
.c-day.c-checked, .c-day:hover {
    color: #FFFFFF;
    background: #0ea1eb;
}
.c-day.c-disable {
    cursor: not-allowed;
    color: #E1E3E6;
}
.c-day.c-disable:hover {
      background: none;
      color: #E1E3E6;
}



