
.c-option {
  height: 34px;
  line-height: 34px;
  font-size: 14px;
  color: #354052;
  cursor: pointer;
  white-space: nowrap;
}
.c-option .c-icon {
    display: inline-block;
    margin-right: 5px;
}
.c-option:hover {
    background: #F0FAFF;
}
.c-option .c-checkbox, .c-option .c-option-item {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 10px;
}
.c-option .c-option-item {
    display: block;
}
.c-option.disable {
    color: #E1E3E6;
    cursor: not-allowed;
}
.c-option.disable:hover {
      background: none;
}
.c-option.active {
    background: #F0FAFF;
}



