
.c-badge {
  display: inline-block;
  position: relative;
}
.c-badge .c-badge-dot, .c-badge .c-badge-count, .c-badge .c-badge-shine {
    display: inline-block;
    position: absolute;
}
.c-badge .c-badge-dot {
    border-radius: 50%;
    width: 10px;
    height: 10px;
    background: #f55858;
    right: -5px;
    top: -5px;
}
.c-badge .c-badge-count {
    border-radius: 10px;
    background: #f55858;
    color: #FFFFFF;
    right: 0;
    top: -10px;
    padding: 0 6px;
    font-size: 12px;
    min-width: 20px;
    height: 20px;
    line-height: 20px;
    white-space: nowrap;
    transform: translateX(50%);
}
.c-badge .c-badge-shine {
    right: 5px;
    top: 50%;
    margin-top: -7px;
    vertical-align: top;
    border-radius: 50%;
    width: 14px;
    height: 14px;
    line-height: 10px;
    text-align: center;
    background: #f55858;
}
.c-badge .c-badge-shine:after {
      box-sizing: border-box;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 1px solid #f55858;
      content: "";
      -webkit-animation: badgeShine 1.2s infinite ease-in-out;
      animation: badgeShine 1.2s infinite ease-in-out;
}
@keyframes badgeShine {
0% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    opacity: .8;
}
to {
    -webkit-transform: scale(2);
    transform: scale(2);
    opacity: 0;
}
}



